import React from 'react';
import Canvas from './Canvas';
import './Animation.css';
let widthOfWindow = window.innerWidth;


let setParticles = (() => {
    let particleArray = [];
    for (let i = 0; i < 80; i++) {
        let x = Math.random() * window.innerWidth;
        let y = Math.random() * window.innerHeight;
        let size = (Math.random() + 2);
        let color = "black";
        let weight = 0.01;
        particleArray.push({ x, y, size, color, weight })
    }
    return particleArray
})();

class Animation extends React.Component {
    constructor(props) {
        super(props);
        this.state = { mouseX: null, mouseY: null, par: setParticles }

        this.updateAnimationState = this.updateAnimationState.bind(this);
        this.handleMouse = this.handleMouse.bind(this);
    }
    handleMouse(e) {
        this.setState({
            mouseX: e.clientX,
            mouseY: e.clientY,
        })
    }
    componentDidMount() {
        this.rAF = requestAnimationFrame(this.updateAnimationState);
    }

    componentWillUnmount() {
        cancelAnimationFrame(this.rAF);
    }

    updateAnimationState() {
        let newPar = this.state.par;
        const { mouseX, mouseY } = this.state;
        for (let i = 0; i < newPar.length; i++) {
            newPar[i].size -= 0.02;
            if (newPar[i].size < 0) {
                newPar[i].x = (mouseX + ((Math.random() * 20) - 10));
                newPar[i].y = (mouseY + ((Math.random() * 20) - 10));
                newPar[i].size = (Math.random() * 2) + 2;
                newPar[i].weight = (Math.random() * 2) - 0.5;
            }
            newPar[i].y += newPar[i].weight;
            newPar[i].weight -= 0.004;
            if (newPar[i].y > window.innerHeight - newPar[i].size) {
                newPar[i].weight *= -0.1;
            }
        }
        this.setState({
            par: newPar,
            mouseX: undefined,
            mouseY: undefined,
        });
        this.rAF = requestAnimationFrame(this.updateAnimationState);
    }

    render() {
        return <div className='animation' onMouseMove={this.handleMouse} >
            <h1>Strony internetowe</h1>
            <p>Damian Hajdukiewicz</p>
            {widthOfWindow > 1000 ? <Canvas particles={this.state.par} /> : null}
        </div>
    }
}
export default Animation;