import React from 'react';
import './styles/AboutMe.css';
import { fadeInLeftBig, fadeInRightBig, slideInUp } from 'react-animations'
import styled, { keyframes } from 'styled-components';
import damian1 from '../damian1.png';
import hobby1 from '../hobby1.jpg';
import hobby2 from '../hobby2.jpg';
import hobby3 from '../hobby3.jpg';
import premia from '../strona-premia.png';
import kopernik from '../strona-kopernik.jpg';
import github from '../strona-github.jpg';

//dodawanie efektów fade do styled components
const fadeAnimation = keyframes`${fadeInLeftBig}`;
const BouncyDiv = styled.div`
  animation: 2s ${fadeAnimation};
`;
const fadeRight = keyframes`${fadeInRightBig}`;
const BouncyRight = styled.div`
  animation: 4s ${fadeRight};
`;
const fadeDown = keyframes`${slideInUp}`;
const BouncyDown = styled.div`
  animation: 6s ${fadeDown};
`;

const AboutMe = () => {
    return (
        <>
            <div className="about">
                <BouncyDiv>
                    <div className="mycv">
                        <img src={damian1} alt="moje zdjęcie profilowe" />

                        <p> <h3>Nazywam się Damian Hajdukiewicz.</h3>Moją pasją jest tworzenie stron internetowych i programowanie. Specjalizuję się w JavaScript, środowisku React-js, oraz WordPress. Staram się ciągle rozwijać i doskonalić. Mieszkam obecnie na  Podlasiu niedaleko Białegostoku. </p>
                    </div>
                </BouncyDiv>
                <BouncyRight>
                    <div className="myportfolio">
                        <p>Kilka moich projektów:</p>
                        <ul>
                            <li><a target="_blank" rel="noopener noreferrer" href="http://www.premiaband.pl"><img src={premia} alt="zdjęcie strony zespołu premia" /></a></li>
                            <li><a target="_blank" rel="noopener noreferrer" href="http://www.goldpalm.pl"><img src={kopernik} alt="zdjęcie strony kopernik" /></a></li>
                            <li><a target="_blank" rel="noopener noreferrer" href="https://github.com/haydulski"><img src={github} alt="logo ze strony github" /></a></li>
                        </ul>
                    </div>
                </BouncyRight>
                <div className="freetime">
                    <p>Po pracy</p>
                    <BouncyDown>
                        <div className="hobbycontainer">

                            <div className="hobby">
                                <img src={hobby2} alt="moje zdjęcie jak gram na gitarze" />
                                <p>Gram na gitarze</p>
                            </div>
                            <div className="hobby">
                                <img src={hobby3} alt="moje zdjęcie na rowerze" />
                                <p>Jeżdżę rowerem</p>
                            </div>
                            <div className="hobby">
                                <img src={hobby1} alt="zdjęcie z wycieczki" />
                                <p>Podróżuję</p>
                            </div>

                        </div>
                    </BouncyDown>

                </div>
            </div>
        </>
    );
}

export default AboutMe;