import React, { Component } from 'react';
import './Canvas.css';


class Canvas extends Component {
    constructor(props) {
        super(props);
        this.canvasRef = React.createRef();
    }

    componentDidUpdate() {
        const { particles } = this.props;
        const canvas = this.canvasRef.current;
        const ctx = canvas.getContext('2d');
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
        for (let i = 0; i < particles.length; i++) {

            ctx.beginPath()
            ctx.arc(particles[i].x, particles[i].y, particles[i].size, 0, Math.PI * 2, false);
            ctx.fillStyle = particles[i].color;
            ctx.fill();
        }
        let opacityValue = 1;
        for (let i = 0; i < particles.length; i++) {
            for (let j = i; j < particles.length; j++) {
                let distance = ((particles[i].x - particles[j].x) * (particles[i].x - particles[j].x)) + ((particles[i].y - particles[j].y) * (particles[i].y - particles[j].y));
                if (distance < 8000) {
                    opacityValue = 1 - (distance / 6000);
                    ctx.strokeStyle = 'rgba(0,0,0,' + opacityValue + ')';
                    ctx.beginPath();
                    ctx.lineWidth = 1;
                    ctx.moveTo(particles[i].x, particles[i].y)
                    ctx.lineTo(particles[j].x, particles[j].y)
                    ctx.stroke();
                };
                // console.log(distance);
            }
        }
    }
    render() {
        return (<>
            <canvas ref={this.canvasRef}></canvas>
        </>);
    }
}

export default Canvas;