import React from 'react';
import './styles/Offer.css';
import CountUp from 'react-countup';
import programy from '../programy.png';
import seologo from '../seologo.png';
import rwdlogo from '../rwdlogo.png';

const Offer = () => {
    return (
        <div className='offer'>
            <header>Witaj !</header>
            <h1>Zamów swoją stronę internetową</h1>
            <div className="offermain">
                <div>
                    <h2>Najnowsze technologie</h2>
                    <img src={programy} alt="zdjęcie języków programowania" />
                    <p>W zależności od wymagań wykonam stronę w optymalnej technologii. Będzie atrakcyjna wizualnie, wydajna, zapewni satysfakcję użytkownikom. </p>

                </div>
                <div>
                    <h2>Optymalizacja pozycjonowania</h2>
                    <img src={seologo} alt="logo pozycjonowania strony" />
                    <p>Dzięki odpowiednim technikom w pisaniu kodu strona będzie wysoko pozycjonowana w wynikach wyszukiwarek </p>

                </div>
                <div>
                    <h2>Responsywny designing</h2>
                    <img src={rwdlogo} alt="logo responsive web designing" />
                    <p>Moje strony i aplikacje są zgodne ze standardami Responsive Web Design. Zaprojektowany szablon dostosuje się do urządzenia na jakim jest wyświetlany </p>

                </div>
            </div>
            <div className="promo">
                Ceny już od <span>{<CountUp start={0} end={500} duration={20} />}</span> złotych!
            </div>
        </div>
    );
}

export default React.memo(Offer);