import React from 'react';
import './styles/Contact.css';
import { useFormik } from 'formik';

const validate = values => {
    const errors = {};
    if (!values.firstName) {
        errors.firstName = 'pole wymagane';
    } else if (values.firstName.length > 15) {
        errors.firstName = 'za dużo liter';
    }

    if (!values.lastName) {
        errors.lastName = 'pole wymagane';
    } else if (values.lastName.length > 20) {
        errors.lastName = 'za dużo liter';
    }

    if (!values.email) {
        errors.email = 'email wymagany';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
        errors.email = 'niepoprawny adres email';
    }
    if (!values.message) {
        errors.message = 'email wymagany';
    } else if (values.message.length < 10) {
        errors.message = 'napisz coś';
    }

    return errors;
}

const Contact = () => {

    const formik = useFormik({
        initialValues: {
            firstName: '',
            lastName: '',
            email: '',
            message: ''
        },
        validate,
        onSubmit: values => {
            alert("Wiadomość została wysłana");
            formik.resetForm()
        },
    });
    return (
        <div className="contactMain">
            <h3>Bądźmy w kontakcie</h3>
            <form onSubmit={formik.handleSubmit}>

                <label htmlFor="firstName">Imię</label>
                <input
                    id="firstName"
                    name="firstName"
                    type="text"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.firstName}
                />
                {formik.touched.firstName && formik.errors.firstName ? (
                    <div>{formik.errors.firstName}</div>
                ) : null}


                <label htmlFor="lastName">Nazwisko</label>
                <input
                    id="lastName"
                    name="lastName"
                    type="text"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.lastName}
                />
                {formik.touched.lastName && formik.errors.lastName ? (
                    <div>{formik.errors.lastName}</div>
                ) : null}


                <label htmlFor="email">Adres email</label>
                <input
                    id="email"
                    name="email"
                    type="email"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.email}
                />
                {formik.touched.email && formik.errors.email ? (
                    <div>{formik.errors.email}</div>
                ) : null}

                <label htmlFor="message">Wiadomość</label>
                <textarea
                    id="message"
                    name="message"
                    type="textarea"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.message}
                />
                {formik.touched.message && formik.errors.message ? (
                    <div>{formik.errors.message}</div>
                ) : null}
                <button type="submit" >Submit</button>

            </form>

        </div>
    );
}
export default Contact;